/* Boradr.js

 border: '2px solid darkblue',
  borderRadius: '10px',
  width: '250px',
  height: '250px',
  margin: '0 auto',
  display: 'grid',
  gridTemplate: 'repeat(3, 1fr) / repeat(3, 1fr)'
  
*/

/*  square.js



*/

:root {
  --fontSize: 1.5em;
  --color: white;
  --background: linear-gradient(90deg, rgb(47, 84, 69), rgb(18, 95, 31));
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background: var(--background);
  color: var(--color);
  overflow: hidden;
}

.curve1 {
  background-color: rgba(230, 239, 227, 0.34);
  width: 19%;
  height: 40%;
  position: absolute;
  border-radius: 50%/10% 50% 0;
  left: 10px;
  top: 20px;
  animation: curve1Move 3s ease-in-out;
  z-index: 3;
}
.curve2 {
  background-color: rgba(225, 217, 132, 0.6);
  width: 19%;
  height: 40%;
  position: absolute;
  /* border-radius: 50%/10% 50% 0; */
  border-radius: 0 50% 10%/50%;
  /* border-radius: 10px 100px / 120px; */
  right: 10px;
  bottom: 10px;
  animation: curve2Move 3s ease-in-out;
  z-index: 4;
}

/*  === Intro section === */
.intro {
  border-radius: 10% 30% 50% 70%;
  background-color: rgb(49, 72, 49);
  width: 50%;
  height: 100vh;
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  gap: 40px;
  justify-content: center;
  text-align: center;
  animation: move 2s ease;
}
/* rgb(64, 94, 201) */
.intro button {
  width: max-content;
  padding: 15px 40px;
  align-self: center;
  font-size: 1.2em;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  background: rgb(4, 165, 4);
  color: white;
  box-shadow: -1px 3px 4px black;
  transition: all 0.3s ease;
}
.intro button:hover {
  background: rgb(64, 94, 201, 0.1);
  box-shadow: -1px 3px 4px black, inset 1px 1px 1px lightslategray;
}
.intro button:active {
  box-shadow: none;
}

/* === Square ===  */
/* rgb(176, 230, 173) */
.btn {
  background: rgb(24, 61, 46);
  border: 2px solid rgb(247, 247, 247);
  font-size: 3em;
  font-weight: 600;
  cursor: pointer;
  padding: 5px;
  color: white;
}

/* === Board === */
.board {
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
  width: 60%;
  height: 50vh;
  margin: 100px auto 0 auto;
  border: 6px solid white;
  border-radius: 10px;
  animation: boardMove 3s ease-in-out;
}

/*  == Game.js == */
.winner {
  margin: 0 auto;
  width: 40%;
  font-size: var(--fontSize);
  /* border-top: 2px solid white; */
  padding: 10px;
}

/* === Restart curve ===  */
.curve3 {
  background-color: rgb(49, 72, 49);
  width: 100%;
  height: 300px;
  border-radius: 50% 50% 10% 10%;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  gap: 30px;
  padding: 30px;
  animation: downUp 3s ease;
}

.winner-badge {
  font-size: 1.5em;
}

.restart {
  padding: 10px 20px;
  background: linear-gradient(45deg, rgb(101, 186, 106), rgb(2, 120, 73));
  color: var(--color);
  font-size: 0.8em;
  cursor: pointer;
  /* border: 3px solid transparent; */
  border: none;
  border-radius: 10px;
  box-shadow: 1px 2px 3px 4px rgb(16, 15, 15);
}

.restart:hover {
  opacity: 0.9;
}

.restart:active {
  /* transform: scale(1.01); */
  box-shadow: inset 1px 1px 2px 1px black;
}

/* === winnter congratulation === */
.con {
  font-size: 2em;
  margin: 80px 10px 0 10px;
  /* border-top: 4px solid lightblue;
  border-right: 4px solid lightblue;
  border-top-right-radius: 30px; */
}

.conBox {
  width: 500px;
  height: 300px;
  padding: 20px;
  text-align: center;
  background: rgb(1, 2, 2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 25px;
  align-items: center;
  position: absolute;
  /* left: 530px; */
  left: calc(50% - 15rem);
  /* left: 50%;
  transform: translateX(-50%); */
  border-radius: 10px;
  border: 5px solid rgb(70, 125, 70);
  animation: conBoxMove 3s ease-in-out;
}

.conBox .cross {
  background-color: rgb(141, 237, 131);
  border-radius: 10% 30% 10% 30%;
  font-size: 1.5em;
  align-self: flex-end;
  /* position: fixed; */
  /* top: 120px; */
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.conBox .cross:hover {
  background-color: rgba(141, 237, 131, 0.7);
}

.conBox h1,
p {
  background: linear-gradient(90deg, white, rgb(141, 237, 131));
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.conBox h3 {
  font-size: 1.3em;
  background: linear-gradient(90deg, rgb(3, 43, 24), rgb(141, 237, 131));
  /* -webkit-text-fill-color: transparent;
  background-clip: text; */
  border-radius: 10% 80% 20% 30%;
}
/* .conBox p {
  font-size: 0.8em;
} */

.curve1 h2 {
  background: rgb(15, 70, 35);
  /* background-clip: text;
  -webkit-text-fill-color: transparent; */
  width: max-content;
  padding: 20px;
  border-radius: 50%/10% 50% 0;
  margin: 20px 5px 0 40px;
  font-size: 2.4em;
  text-shadow: 2px 3px 10px black;
}

/* === Sorry for who is not winning === */
.curve2 h1 {
  margin-left: 40px;
}
.curve2 h2 {
  background: rgb(70, 24, 15);
  /* background-clip: text;
  -webkit-text-fill-color: transparent; */
  width: max-content;
  padding: 20px;
  border-radius: 50%/10% 50% 0;
  margin: 20px 5px 0 50px;
  font-size: 2.4em;
  text-shadow: 2px 3px 10px black;
}

.curve2 h3 {
  margin: 10px 5px 0 50px;
  color: rgb(81, 2, 2);
  font-size: 0.9em;
}

/*  === Intor Animation === */

/* prettier-ingnore */
@keyframes move {
  from {
    clip-path: polygon(0 0, 80% 0%, 17% 25%, 0% 100%);
  }
  /* prettier-ingnore */
  to {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }
}

/* === Winner section Animation === */

@keyframes downUp {
  /* prettier-ingnore */
  from {
    clip-path: polygon(
      49% 65%,
      83% 70%,
      100% 67%,
      100% 70%,
      100% 100%,
      50% 100%,
      0 100%,
      0% 70%,
      0 64%,
      26% 69%
    );
  }
  to {
    clip-path: polygon(
      51% 0,
      77% 4%,
      100% 26%,
      100% 69%,
      100% 100%,
      50% 100%,
      0 100%,
      0% 70%,
      0 28%,
      24% 3%
    );
  }
}

@keyframes curve1Move {
  0% {
    transform: translateX(-30px);
  }
  50% {
    transform: translate(400px, 200px) scale(1.5);
    /* transform: scale(1.2); */
  }
  /* 100% {
    transform: translate(0px) scale(1);
  } */
}

@keyframes curve2Move {
  /* 0% {
    transform: translate(120px);
  } */
  50% {
    transform: translate(-400px, -200px) scale(1.5);
  }
}

/*  === Game Board === */
@keyframes boardMove {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.2);
  }
}

/* === Con box Move Animation === */
@keyframes conBoxMove {
  0% {
    transform: translateY(-90px);
  }
  50% {
    transform: translateY(0px);
  }
}

/*  ==== Media for small Device ==== */
@media only screen and (max-width: 1060px) {
  .intro {
    width: 60%;
    height: 80vh;
  }

  .restart {
    font-size: 0.5em;
  }
  .winner-badge {
    font-size: 1em;
  }
  .winner {
    width: 70%;
  }

  .confor-winner-curve1 {
    display: none;
  }
  .curve1 {
    width: 15%;
    height: 20%;
  }
  .curve2 {
    width: 15%;
    height: 20%;
  }
  .conBox {
    /* left: 46px; */
    width: 300px;
    left: calc(50% - 9rem);
  }
}

@media only screen and (max-width: 600px) {
  .intro {
    width: 50%;
    height: 80vh;
    margin-top: 60px;
    padding: 30px;
    font-size: 0.8em;
  }
}
